import React, {useState} from "react";
import { CloseOutlined } from "@ant-design/icons";
import citiesData from "./citiesData.json"

const PopupScreen = () => {
    const [selectedCity] = useState('');
    const [selectedLocation, setSelectedLocation] = useState('');


    // const handleCityChange = ({e}: any) => {
    //     const cityId = e.target.value;
    //     setSelectedCity(cityId);

    //     const city = citiesData.find(city => city.id === parseInt(cityId));
    //     setIsLocation(city ? city.locations : []);
    //     setSelectedLocation("");
    // }

    return (
        <div className="flex justify-center">
            <div className="w-[700px] h-[200px] inline-block bg-white mt-40">
                <div className="flex justify-end ">
                    <button 
                        className="absolute z-10 w-[44px] text-slate-400 mt-2">
                            <CloseOutlined />
                    </button>
                </div>
                <div className="flex flex-row items-center flex-1">
                    <div className="flex-1 ">
                        <div className="flex justify-center mt-16">
                            <text className="text-slate-800 text-base font-bold">Tỉnh/Thành phố</text>
                        </div>
                        <div className="flex justify-center">
                            <select 
                                // id='tinhTP'    
                                // value = {selectedCity}
                                // onChange={handleCityChange}
                                className="w-[60%] h-[25px] bg-white text-slate-800 text-sm mt-4 border border-solid border-black rounded" >
                                <option>
                                    Chọn tỉnh/thành phố
                                </option>
                                {citiesData.map(city => (
                                    <option
                                        key = {city.id}
                                        value = {selectedCity} >
                                        {city.name}
                                    </option>
                                ))}
                            </select>
                        </div>
                    </div>
                    <div className="flex-1">
                    <div className="flex justify-center mt-16">
                        <text className="text-slate-800 text-sm font-bold ">Rạp</text>
                    </div>
                        <div className="flex justify-center">
                            <select 
                                id='tinhTP'    
                                value = {selectedLocation}
                                onChange={(e) => setSelectedLocation(e.target.value)}
                                className="w-[60%] h-[25px] bg-white text-slate-800 text-sm mt-4 border border-solid border-black rounded" >
                                <option >
                                    Chọn chi nhánh
                                </option>
                                {citiesData.map ((city) => (
                                    <option
                                        key = {city.id}
                                        value = {selectedLocation} >
                                        {city.locations}
                                    </option>
                                ))}
                            </select>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default PopupScreen;